import React, { Component } from "react";

import "./champions.css";

class Champions extends Component {
    constructor() {
        super();

        this.state = {
            champions: [],
            loadingFinished: false,
            errorOccured: false
        }
    }

    componentDidMount() {
        fetch("/champions", {
            headers: {
                "Accept": "application/json",
                "Content-Type": "application/json"
            },
            method: "get"
        })
        .then(res => {
            res.json().then(champions => {
                this.setState({ champions, loadingFinished: true, errorOccured: false });
            });
        })
        .catch(err => {
            this.setState({ errorOccured: true });
        });
    }

    render() {
        return (
        <div id="champions">
            <p>{this.state.champions.length}</p>
        </div>
        );
    }
}

export default Champions;
